<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
      </div>
      <div class="wrapper">
        这里是新闻详情
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import {getQuery} from 'common/js/util'

  export default {
    data () {
      return {

      }
    },
    created () {
      this._getDetail()
    },
    methods: {
      _getDetail(){
        let id = getQuery('id')
        if(!id){
          return
        }
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active, .slide-leave-active
    transition all 0.3s

  .slide-enter, .slide-leave-to
    transform translate3d(100%, 0, 0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 50px
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .text
        color $color-header-text
        font-size $font-size-large
      .icon
        background url("~common/image/header/message.png") no-repeat
        position absolute
        height 14px
        width 17px
        right 16px
        top 15px
        background-size contain
        &:after
          position absolute
          background #eb4747
          width 6px
          height 6px
          border-radius 3px
          top -4px
          right -2px
          content ''
    .wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      justify-content center

</style>



